/*eslint-disable*/
export default {

    state: {
        purchase_type: [],
    },
    mutations: {
        setPurchaseType(state, purchase_type) {
            state.purchase_type = purchase_type;
        },
    },
    actions: {
        async fetchPurchaseType({ commit }) {
            try {
                const response = await axios.get('/api/purchase_type');
                const data = response.data;
                commit('setPurchaseType', data);
            } catch (err) {
                console.error(err);
            }
        },
    },

}