/*eslint-disable */

// Vue
import Vue from 'vue'

// Iconos
import FeatherIcon from '@core/components/feather-icon/FeatherIcon.vue'

// Bootstrap-Vue
import BootstrapVue from 'bootstrap-vue'

// Validaciones Formularios
import * as Validator from 'vee-validate'
import es from 'vee-validate/dist/locale/es.json'
import { ValidationProvider, ValidationObserver } from 'vee-validate'

// Diseo para las tablas
import VueGoodTablePlugin from 'vue-good-table'

// Loading para las tablas
import Loading from '@/components/loading/Loading.vue'

// Componente de validacin de errores del back
import ErrorValidation from '@/views/error/ErrorValidation.vue'

// Iconos
Vue.component(FeatherIcon.name, FeatherIcon)

// Bootstrap-Vue
Vue.use(BootstrapVue)

// Validaciones Formularios
Validator.localize("es", es);

Vue.component('ValidationProvider', ValidationProvider)
Vue.component('ValidationObserver', ValidationObserver)

// Loading
Vue.component('loading', Loading)

// Diseo para las tablas
Vue.use(VueGoodTablePlugin)

// Validacin de errores del back
Vue.component('ErrorValidation', ErrorValidation)
