<template>
<!-- eslint-disable -->
    <div v-if="validationErrors">
        <ul class="alert alert-warning px-2 py-2 my-2">
            <li v-for="(value, key, index) in validationErrors" :key="index">{{ value }}</li>
        </ul>
    </div>
</template>

<script>

/*eslint-disable */
    export default {

        name: 'ErrorValidation',

        props: ['errors'],

        computed: {
            validationErrors() {
                let errors = Object.values(this.errors);
                errors = errors.flat();
                return errors;
            }
        }
    }

</script>
